
'use client';
import { useState, useEffect } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { db } from '@/lib/firebase'; // <-- CORREGIDO
import { collection, getDocs, query, where, Timestamp } from 'firebase/firestore';

const TurnosPage = () => {
  const { userRole } = useAuth();
  const [turnos, setTurnos] = useState({ hoy: [], futuros: [], pasados: [] });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchTurnos = async () => {
      try {
        const turnosCollection = collection(db, 'turns'); // <-- CORREGIDO
        const hoy = new Date();
        const inicioHoy = new Date(hoy.setHours(0, 0, 0, 0));
        const finHoy = new Date(hoy.setHours(23, 59, 59, 999));

        const qHoy = query(turnosCollection, where('date', '>=', Timestamp.fromDate(inicioHoy)), where('date', '<=', Timestamp.fromDate(finHoy)));
        const qFuturos = query(turnosCollection, where('date', '>', Timestamp.fromDate(finHoy)));
        const qPasados = query(turnosCollection, where('date', '<', Timestamp.fromDate(inicioHoy)));

        const [hoySnapshot, futurosSnapshot, pasadosSnapshot] = await Promise.all([
          getDocs(qHoy),
          getDocs(qFuturos),
          getDocs(qPasados)
        ]);

        setTurnos({
          hoy: hoySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })),
          futuros: futurosSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })),
          pasados: pasadosSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }))
        });

      } catch (error) {
        console.error("Error al obtener los turnos: ", error);
      }
      setLoading(false);
    };

    if (userRole === 'admin') {
      fetchTurnos();
    }
  }, [userRole]);

  if (userRole !== 'admin') {
    return (
      <div className="flex justify-center items-center h-full">
        <p className="text-red-500">No tienes permiso para ver esta página.</p>
      </div>
    );
  }

  if (loading) {
    return <p>Cargando turnos...</p>;
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-8">Gestión de Turnos</h1>
      
      {/* Turnos de Hoy */}
      <div className="mb-12">
        <h2 className="text-2xl font-semibold mb-4">Turnos de Hoy</h2>
        {turnos.hoy.length > 0 ? (
          <div className="bg-white shadow-md rounded">
            <table className="min-w-full leading-normal">
              {/* ... encabezado de la tabla ... */}
              <tbody>
                {turnos.hoy.map(turno => (
                  <tr key={turno.id}>
                    {/* ... celdas de la tabla ... */}
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        ) : <p>No hay turnos para hoy.</p>}
      </div>

      {/* Turnos Futuros */}
      <div className="mb-12">
        <h2 className="text-2xl font-semibold mb-4">Turnos Futuros</h2>
        {/* ... similar a turnos de hoy ... */}
      </div>

      {/* Turnos Pasados */}
      <div>
        <h2 className="text-2xl font-semibold mb-4">Turnos Pasados</h2>
        {/* ... similar a turnos de hoy ... */}
      </div>

    </div>
  );
};

export default TurnosPage;
